@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Open+Sans:wght@300;400;600&display=swap);
:root {
  --black: rgb(47, 48, 51);
  --white: #f8f8f8;
  --gold: rgb(200,165,106);
  --copper: rgb(231, 147, 82);;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(47, 48, 51);
  background-color: var(--black);
}

.App {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  min-height: 100vh;
}

nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 2rem;
  position: absolute;
  top: 0;
  width: 100%;
}

nav p {
  -webkit-flex-basis: 6rem;
          flex-basis: 6rem;
  font-size: .8rem;
  color: rgb(200,165,106);
  color: var(--gold);
}

.heroSection {
  color: #f8f8f8;
  color: var(--white);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 3rem;
  width: 100%;
}

.nameAboutWrapper{
  width: 85%;
  margin-top: 3rem;
}

.fullName {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 2.5rem;
  padding-top: 2rem;
}

.fullName p {
  margin: 0 0 .5rem;
  text-align: left;
  
}

.about {

  font-size: 1rem;
  text-align: right;
  margin: 3.5rem 0 0;
}

.socialWrapper {
  display: -webkit-flex;
  display: flex;
  width: 85%;
  padding: 4rem 0 7rem;
}

.social {
  font-size: 1.8rem;
  -webkit-flex-basis: 15%;
          flex-basis: 15%;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: rgb(200,165,106);
  color: var(--gold);
  font-size: 0.8rem;
  padding: 2rem 0 3rem;
}

.reactLogo {
  font-size: 1.2rem;
  margin-left: 0.2rem;
}

@media (min-width: 375px) {

  .fullName {
    font-size: 3rem;
  }
  
  .about {
    font-size: 1.05rem;
  }

  .social {
    font-size: 2.2rem;
  }
}

@media (min-width: 600px) {
  .fullName{
    font-size: 3.6rem;
  }
}

@media (min-width: 768px) {

  .heroSection {
    padding-top: 5rem;
  }

  .nameAboutWrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;

  }

  .fullName {
    font-size: 3.6rem;
    padding: 0;
  }

  .about {
    margin: 0;
    width: 17rem;
    padding-bottom: 1rem;
    font-size: 1.2rem;
  }

  .socialWrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    padding: 4rem 0 7rem;

  }

  .social {
    -webkit-flex-basis: 4rem;
            flex-basis: 4rem;
  }
}

@media (min-width: 1024px) {

  .fullName {
    font-size: 5rem;
  }

  .about {
    width: 20rem;
    font-size: 1.4rem;   
  }

  .social {
    font-size: 2.5rem;
    -webkit-flex-basis: 4.5rem;
            flex-basis: 4.5rem;
  }

  .home:hover,
  .projects:hover,
  .contacts:hover {
    color: #f8f8f8;
    color: var(--white);
    cursor: pointer;
  }

  .name:hover,
  .surname:hover,
  .about:hover {
    color: rgb(200,165,106);
    color: var(--gold);
    cursor: default;
  }

  .linkedin:hover,
  .github:hover,
  .codepen:hover {
    color: rgb(200,165,106);
    color: var(--gold);
    cursor: pointer;
  }

  .footer:hover {
    color: #f8f8f8;
    color: var(--white);
    cursor: default;
  }
}

@media (min-width: 1440px) {

  .nameAboutWrapper{
    width: 90%;
    margin-top: 3rem;
  }

  .fullName {
    font-size: 8rem;
  }

  .about {
    width: 23rem;
    font-size: 1.6rem;   
    padding-bottom: 3rem;
  }

  .socialWrapper{
    padding: 4.5rem 0 7rem;
  }

  .social {
    font-size: 2.7rem;
    -webkit-flex-basis: 5rem;
            flex-basis: 5rem;
  }
}

@media (min-width: 1877px) {
  
  .nameAboutWrapper{
    width: 70%;

  }
}


